/////////////////////
/// Now playing child view
/// - Parent is sidebar
/////////////////////

class NowPlayingView extends Backbone.View {

  initialize() {
    this.el = 'album-art';
    this.render();
  }

  render(props) {
    props = props || null;
    React.renderComponent(
      NowPlaying({data: props}),
      document.getElementById('now-playing-wrap')
    )

    return this;
  }

};
/////////////////////
/// App parent view
/// - TopbarView
/// - SidebarView
/// - TracksView
/// - CommentsView
/// - ControlsView
/////////////////////

class PlayerView extends Backbone.View {

  initialize() {
    this.el = 'player';
    this.render();

    // Create the high level parent views
    // These views will create and populate their own children
    // Which is exactly what this view is doing
    App.Views.TopbarView    = new TopbarView();
    App.Views.SidebarView   = new SidebarView();
    App.Views.TracksView    = new TracksView({collection: App.Collections.FavoritesCollection});
    App.Views.UserBlurbView = new UserBlurbView();
    App.Views.ControlsView  = new ControlsView();
  }

  render() {
    React.renderComponent(
      Player({}),
      document.getElementById('app')
    )

    return this;
  }

};
/////////////////////
/// Sidebar parent view
/// - AlbumArtView
/////////////////////

class SidebarView extends Backbone.View {

  initialize() {
    this.el = 'sidebar';
    this.render();

    // Create child views
    // this.setsView  = new SetsView();
    App.Views.NowPlayingView = new NowPlayingView();
    App.Views.CommentsView   = new CommentsView();
  }

  render() {
    React.renderComponent(
      Sidebar({}),
      document.getElementById('sidebar-wrap')
    )

    return this;
  }

};
/////////////////////
/// Topbar child view
/// - Parent is player
/////////////////////

class TopbarView extends Backbone.View {

  initialize() {
    this.el = 'topbar';
    this.render();
  }

  render() {
    React.renderComponent(
      Topbar({username: readCookie('username'), avatarUrl: readCookie('avatarUrl')}),
      document.getElementById('topbar-wrap')
    )

    return this;
  }

};
/////////////////////
/// Tracks parent view
/////////////////////

class TracksView extends Backbone.View {

  initialize() {
    this.el = 'tracks';
    this.render();
    this.listenTo(this.collection, 'reset', this.render);
  }

  render() {
    var data = this.collection.toJSON() || [];

    React.renderComponent(
      Tracks(data.length ? {data: data} : {}),
      document.getElementById('tracks-wrap')
    )

    return this;
  }

};
